import React from 'react';

class About extends React.Component {
  render() {
    return (
      <section id="about" className="about">
        <div className="about-decor">
          <div className="about-circle1">
            <img src="assets/images/team1.png" alt="team1" />
          </div>
          <div className="about-circle2">
            <img src="assets/images/main-banner1.png" alt="banner1" />
          </div>
        </div>
        <div className="container">
          <div className="row ">
            <div className="col-md-5">
              <div className="about-contain">
                <div>
                  <h2 className="title">
                    about <span>GrowthSayer</span>
                  </h2>
                  <p className="caption-about">
                    Growthsayer is an AI-Powered demand forecasting and planning
                    platform that revolutionizes the way the cannabis industry
                    thinks about product growth.
                  </p>
                  <div className="row sm-mb">
                    <div className="col-6">
                      <ul className="about-style">
                        <li className="abt-hover">
                          <div className="about-icon">
                            <div className="icon-hover">
                              <img
                                src="assets/images/icon1.png"
                                alt="AI Powered"
                              />
                            </div>
                          </div>
                          <div className="about-text">
                            <h3>AI/ML Powered Predictions</h3>
                          </div>
                        </li>
                        <li className="abt-hover">
                          <div className="about-icon">
                            <div className="icon-hover">
                              <img
                                src="assets/images/icon3.png"
                                alt="24x7 Support"
                              />
                            </div>
                          </div>
                          <div className="about-text">
                            <h3>24x7 Support</h3>
                          </div>
                        </li>
                      </ul>
                    </div>
                    <div className="col-6">
                      <ul className="about-style">
                        <li className="abt-hover">
                          <div className="about-icon">
                            <div className="icon-hover">
                              <img
                                src="assets/images/icon2.png"
                                alt="Awasome-Design"
                              />
                            </div>
                          </div>
                          <div className="about-text">
                            <h3>Inventory optimizations</h3>
                          </div>
                        </li>
                        <li className="abt-hover">
                          <div className="about-icon">
                            <div className="icon-hover">
                              <img
                                src="assets/images/icon4.png"
                                alt="KPI Dashboards"
                              />
                            </div>
                          </div>
                          <div className="about-text">
                            <h3>KPI Dashboards</h3>
                          </div>
                        </li>
                      </ul>
                    </div>
                  </div>
                  <div className="top-margin" />
                </div>
              </div>
            </div>
            <div className="col-md-7 d-medium-none">
              <div className="about-right">
                <div className="about-phone">
                  <img
                    src="assets/images/aboutus.png"
                    className="img-fluid"
                    alt="aboutus"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default About;



