import React from 'react';
import Tilt from 'react-tilt';

class Landing extends React.Component {
  render() {
    return (
      <section id="home" className="home">
        <div className="home-decor">
          <div className="home-circle1">
            <img src="assets/images/main-banner3.png" alt="" />
          </div>
          <div className="home-circle2">
            <img src="assets/images/main-banner12.png" alt="" />
          </div>
          <div className="home-circle3">
            <img src="assets/images/main-banner1.png" alt="" />
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-md-5">
              <div className="home-contain">
                <div>
                  <img
                    src="assets/images/logoimage.png"
                    alt="caption-img"
                    className="caption-img"
                  />
                  <h4>Predict the future with</h4>
                  <h1>
                    <span className="f-bold">Growth</span>
                    <span className="f-bold f-color">sayer</span>
                  </h1>
                  <p>
                    The world's best AI powered forecasting tool specifically
                    for the legal cannabis industry.
                  </p>
                </div>
              </div>
            </div>
            <div className="col-sm-6 offset-md-1">
              <div className="home-right">
                <Tilt
                  options={{
                    perspective: 110,
                    speed: 400,
                    max: 1.2,
                    scale: 1,
                  }}
                >
                  <img
                    src="assets/images/mobile.png"
                    className="img-fluid"
                    alt="mobile"
                  />
                </Tilt>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Landing;



