import React from 'react';

class Feature extends React.Component {
  render() {
    return (
      <section id="feature" className="feature">
        <div className="feature-decor">
          <div className="feature-circle1">
            <img src="assets/images/feature2.png" alt="" />
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="feature-phone">
              <img src="assets/images/222.png" className="img-fluid" alt="" />
            </div>
            <div className="offset-lg-4 col-lg-8">
              <div className="row">
                <div className="col-sm-12 mrgn-md-top">
                  <h2 className="title">
                    Benefits of<span> growthsayer</span>
                  </h2>
                </div>
                <div className="col-12 col-md-6">
                  <ul className="feature-style">
                    <li>
                      <div className="feature-icon">
                        <img src="assets/images/icon/3.png" alt="icon" />
                      </div>
                      <div className="feature-subtitle">
                        <h3>Maximize ROI</h3>
                      </div>
                      <div>
                        <p>Optimize margins by adjusting price and demand. </p>
                      </div>
                    </li>
                    <li>
                      <div className="feature-icon">
                        <img src="assets/images/icon/1.png" alt="icon" />
                      </div>
                      <div className="feature-subtitle">
                        <h3>Accurate predictions</h3>
                      </div>
                      <div>
                        <p>
                          Powerful forecasting capabilities drive unit-level
                          accuracy{' '}
                        </p>
                      </div>
                    </li>
                    <li>
                      <div className="feature-icon">
                        <img src="assets/images/icon/5.png" alt="icon" />
                      </div>
                      <div className="feature-subtitle">
                        <h3>Key Integrations</h3>
                      </div>
                      <div>
                        <p>
                          Growthsayer integrates with METRC and your POS
                          software, so all your data is in one place.{' '}
                        </p>
                      </div>
                    </li>
                  </ul>
                </div>
                <div className="col-12 col-md-6 sm-m-top">
                  <ul className="feature-style">
                    <li>
                      <div className="feature-icon">
                        <img src="assets/images/icon/2.png" alt="icon" />
                      </div>
                      <div className="feature-subtitle">
                        <h3>actionable insights</h3>
                      </div>
                      <div>
                        <p>
                          Get tailored insights about your forecast, with
                          specific recommendations for adjustments.
                        </p>
                      </div>
                    </li>
                    <li>
                      <div className="feature-icon">
                        <img src="assets/images/icon/4.png" alt="icon" />
                      </div>
                      <div className="feature-subtitle">
                        <h3>time savings</h3>
                      </div>
                      <div>
                        <p>
                          With auto generating forecasts, most of the work is to
                          review and approve.{' '}
                        </p>
                      </div>
                    </li>
                    <li>
                      <div className="feature-icon">
                        <img src="assets/images/icon/6.png" alt="icon" />
                      </div>
                      <div className="feature-subtitle">
                        <h3>Hassle free</h3>
                      </div>
                      <div>
                        <p>
                          With our cloud based platform, you are always on the
                          latest version of the software. And setting up with
                          your POS system is a breeze.{' '}
                        </p>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Feature;



