import React from 'react';
import OwlCarousel from 'react-owl-carousel';

class Testimonial extends React.Component {
  render() {
    // OwlCarousel Option for Testimonial
    const options = {
      0: {
        items: 1,
        dots: true,
        nav: false
      },
      600: {
        items: 1,
        dots: true,
        nav: false
      },
      991: {
        items: 1,
        margin: 15
      },
      1000: {
        items: 1
      }
    };

    // Dynamic Testimonial Easy to Update
    let data = [
      {
        name: 'Alejandro Salas',
        designation: 'Inventory Directory, Tincture manufacturer',
        photo: '22.jpg',
        description:
          'Growthsayer is a game changer for sure. Since we’ve started using the optimization reports our we’ve doubled our margins and improved our in-stocks by 40%.'
      },
      {
        name: 'Kevin Welty',
        designation: 'Product Planning Manager, concentrates and extracts',
        photo: '15.jpg',
        description:
          'Expanding our product offerings used to be mostly trial and error. It was always difficult to know how popular at item would be. It’s amazing how accurate the forecasts are for our new product launches. This thing is a crystal ball!'
      },
      {
        name: 'Chris Sanderson',
        designation: 'Owner, 3Lux Edibles',
        photo: '1.jpg',
        description:
          'With aggressive growth targets, navigating this market can be incredibly complicated. The insights provided by growthsayer are critical toward building a strong financial roadmap. We’re on a 3 year growth trajectory to 5x the size of our business as we enter new markets.'
      }
    ];

    // Dynamic Testimonial Data Loop
    let DataList = data.map((val, i) => {
      return (
        <div className="testimonial-item" key={i}>
          <div className="testimonial-block">
            <div className="testimonial-avtar">
              <img src={`assets/images/avtar/${val.photo}`} alt="" />
            </div>
            <div className="testimonial-text">
              <p>{val.description}</p>
              <h3>{val.name}</h3>
              <h6>{val.designation}</h6>
            </div>
          </div>
        </div>
      );
    });

    return (
      <section id="testimonial" className="testimonial">
        <div className="testimonial-decor">
          <div className="testi-circle1">
            <img src="assets/images/Testimonial2.png" alt="" />
          </div>
          <div className="testi-circle2">
            <img src="assets/images/Testimonial1.png" alt="" />
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-lg-10 offset-lg-1">
              <OwlCarousel
                className="testimonial-carousel owl-carousel owl-theme"
                loop={true}
                margin={5}
                nav={true}
                navClass={['owl-prev', 'owl-next']}
                navText={[
                  '<img src="assets/images/back.png">',
                  '<img src="assets/images/next.png">'
                ]}
                dots={false}
                responsive={options}
              >
                {DataList}
              </OwlCarousel>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Testimonial;



